}

#back {
    width: 100%;
    height: 100vh;
}

#back img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

#top {
    pointer-events: none;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    position: absolute;
    /* background: black; */
}

#workingArea {
    position: relative;
    margin: 0 auto;
    width: 80%;
    height: 100vh;
    max-width: 1600px;
    /* border: 2px solid red; */
}