#hero {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(0%, -50%);
    display: flex;
    justify-content: space-between;
    width: 100%;
    /* height: 100px; */
    /* background: red; */
}

#heroLeft {
    min-width: 50%;
    /* height: 100px; */
    /* background-color: yellow; */
}

#heroRight {
    width: 25%;
    /* height: 100px; */
    /* background-color: green; */
    color: #fff;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

#heroLeft #elem h1{
    /* font-family: 'Times New Roman', Times, serif; */
    color: #fff;
    font-size: 7vw;
    /* line-height: 0.9; */
}

#heroLeft #elem{
    height: 9vw;
    /* background-color: red; */
    overflow: hidden;
}

#heroLeft button{
    margin: 1vw 0;
    padding: 0.8vw 2.5vw;
    font-size: 2vw; 
    pointer-events: all;
}

#heroRight p{
    text-align: end;
    font-size: 10px;
    font-weight: 200;
    margin-bottom: 30px;
}

#heroRight #imageDiv{
    height: 170px;
    width: 100%;
    background-color: whitesmoke;
    margin-top: 10px;
    margin-bottom: 100px;
    background-image: url(https://upload.wikimedia.org/wikipedia/commons/thumb/5/5c/20191207_City_Palace%2C_Udaipur_1701_7325.jpg/417px-20191207_City_Palace%2C_Udaipur_1701_7325.jpghttps://upload.wikimedia.org/wikipedia/commons/thumb/5/5c/20191207_City_Palace%2C_Udaipur_1701_7325.jpg/417px-20191207_City_Palace%2C_Udaipur_1701_7325.jpg);
    background-size: cover;
    background-position: center;
}

#heroRight p:nth-child(2){
    margin-bottom: 100px;
}